(function() {
  'use strict';

  angular
    .module('palomitasClient2')
    .component('navbar', navbar());

  /** @ngInject */
  function navbar() {
    var component = {
      templateUrl: 'app/components/navbar/navbar.html',
      controller: NavbarController,
      controllerAs: 'vm'
    };

    return component;

    /** @ngInject */
    function NavbarController($http) {
      var vm = this;
      vm.searchRes;
      vm.searchText = "";
      vm.search = search;
      vm.parseSearch = parseSearch;
      
      function search(query){
        if(!query || query.length < 2){
          return;
        }
        var url = "https://tvapi.fuken.xyz"
        url += "/search?query="+query;
        return $http.get(url).then(function(res){
          vm.searchRes = res.data;
          return res.data;
        })
      }
      
      function parseSearch(obj){
        if(!angular.isObject(obj) ||
           !obj.show || 
           !obj.show.externals || 
           !obj.show.externals.imdb){
          return null;
        }
        return /*obj.show.externals.imdb+": "+*/ obj.show.name;
      }
      
    }
  }

})();

(function(){
  'use strict';
  
  angular
    .module('palomitasClient2')
    .controller('ShowController', ShowController);
    
  /** @ngInject */
  function ShowController($stateParams, $http, $httpParamSerializer, lodash, $window, $rootScope){
    var vm = this;
    var _  = lodash;
    
    vm.id = $stateParams.id;
    vm.show     = {};
    vm.episodes = [];
    vm.langs    = [];
    vm.selectedSeason  = [];
    vm.selectedEpisode = {};
    vm.selectedLang    = "";
    vm.showMagnet = false;
    vm.loading    = false;

    vm.space = space;
    vm.setEpisode = setEpisode;    
    vm.scrollBack = scrollBack;
    vm.loadSubtitles = loadSubtitles;

    activate();
    
    // PUBLIC METHODS
    
    function space(obj){
      return angular.toJson(obj, true);
    }
    
    function setEpisode(ep){
      vm.selectedEpisode = ep;
      var el = document.querySelector(".episodes");
      angular.element(el).scrollLeft($window.innerWidth, 400);
    }
    
    function scrollBack(){
      var el = document.querySelector(".episodes");
      angular.element(el).scrollLeft(0, 400);
    }
    
    function loadSubtitles(episode){
      vm.langs = $rootScope.langs;
      var api = "https://sub-down.fuken.xyz";
      var url = api+"/search?"+$httpParamSerializer({
        imdbid: vm.id,
        season: episode.season,
        episode: episode.episode
      });

      vm.loading = true;
      $http.get(url).then(function onSubtitles(res) {
        vm.subtitles = res.data;
        vm.loading = false;
      })
    }

    // PRIVATE METHODS
    
    function activate(){
      var api = "https://api-fetch.website/tv/";
      var url = api+"/show/"+vm.id;
      $http.get(url).then(function(res){
        vm.show = res.data;
        vm.episodes = getEpisodes(res.data);
        vm.selectedSeason = vm.episodes[0].episodes;
        vm.selectedEpisode = vm.selectedSeason[0];
      });
    }
    
    function getEpisodes(show){
      var eps = show.episodes;
      var grouped = _.groupBy(eps, 'season');
      var moreGrouped = Object.keys(grouped).map(function(index){
        var season = grouped[index];
        var sortedSeason = _.sortBy(season, 'episode');
        return { number: sortedSeason[0].season, episodes: sortedSeason };
      });
      return moreGrouped;
    }
    
    
    
  }
  
})();
(function() {
  'use strict';

  angular
    .module('palomitasClient2')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(toastr, $http) {
    var vm = this;
    var api = "https://tvapi.fuken.xyz"

    vm.creationDate = 1463518051145;
    vm.showData = null;
    vm.error = null;

    vm.showToastr = showToastr;
    vm.showlist = function(){};

    activate();

    function activate(){
      $http.get(api+"/shows")
        .then(function(res){
          vm.showData = res.data;
          vm.error = null;
        })
        .catch(function(err){
          vm.error = err;
        });
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('palomitasClient2')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $http, $rootScope) {
    $log.debug('Palomitas V2');
    var api = "https://sub-down.fuken.xyz";
    var url = api+"/weblangs.json";
    $http.get(url).then(function(res){
      $rootScope.langs = res.data;
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('palomitasClient2')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      })
      .state('show_detail', {
        url:'/show/:id',
        templateUrl: 'app/show/show.detail.html',
        controller: 'ShowController',
        controllerAs: 'vm'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

